import React, { useState, useEffect } from 'react';
import { Volume2, Power } from 'lucide-react';
import { audioEngine } from '@/lib/audio-engine-bridge';

interface DawTrackControlProps {
    trackId: string;
    trackName: string;
    color: string;
}

export function DawTrackControl({ trackId, trackName, color }: DawTrackControlProps) {
    const [volume, setVolume] = useState(80); // 0-100
    const [pan, setPan] = useState(0); // -100 (L) to 100 (R)
    const [isMuted, setIsMuted] = useState(false);
    const [isSolo, setIsSolo] = useState(false);

    // Communicate changes to the Wasm Bridge
    useEffect(() => { audioEngine.setTrackVolume(trackId, volume / 100); }, [volume, trackId]);
    useEffect(() => { audioEngine.setTrackPan(trackId, pan / 100); }, [pan, trackId]);
    useEffect(() => { audioEngine.setTrackMute(trackId, isMuted); }, [isMuted, trackId]);
    useEffect(() => { audioEngine.setTrackSolo(trackId, isSolo); }, [isSolo, trackId]);

    // Simple Circular Knob Renderer (SVG)
    const renderKnob = (
        value: number,
        min: number,
        max: number,
        label: string,
        onChange: (v: number) => void,
        isBipolar: boolean = false
    ) => {
        // Map value to angle (-135 to 135 deg)
        const percent = (value - min) / (max - min);
        const angle = -135 + percent * 270;

        // Custom Mouse Drag logic for knobs
        const handleMouseDown = (e: React.MouseEvent) => {
            e.preventDefault();
            const startY = e.clientY;
            const startVal = value;

            const onMouseMove = (moveEvent: MouseEvent) => {
                const deltaY = startY - moveEvent.clientY; // Up is positive
                // Sensitivity: 1 pixel move = 1 unit change
                let newVal = startVal + deltaY;
                newVal = Math.max(min, Math.min(max, newVal));
                onChange(newVal);
            };

            const onMouseUp = () => {
                window.removeEventListener('mousemove', onMouseMove);
                window.removeEventListener('mouseup', onMouseUp);
            };

            window.addEventListener('mousemove', onMouseMove);
            window.addEventListener('mouseup', onMouseUp);
        };

        return (
            <div className="flex flex-col items-center gap-1 group">
                <div
                    className="w-8 h-8 rounded-full bg-[#18181A] shadow-[inset_0_2px_4px_rgba(0,0,0,0.8),0_1px_1px_rgba(255,255,255,0.05)] border border-[#222] relative flex items-center justify-center cursor-ns-resize"
                    onMouseDown={handleMouseDown}
                >
                    {/* Outline track */}
                    <svg className="absolute inset-0 w-full h-full -rotate-[135deg]" viewBox="0 0 32 32">
                        <circle cx="16" cy="16" r="12" fill="none" stroke="#222" strokeWidth="2" strokeDasharray="56.5" />
                        {isBipolar ? (
                            // Pan shows center out
                            <circle cx="16" cy="16" r="12" fill="none" stroke={color} strokeWidth="2" strokeDasharray="56.5" strokeDashoffset={56.5 - (Math.abs(percent - 0.5) * 56.5)} className={`transition-all ${percent > 0.5 ? 'origin-center rotate-[135deg]' : 'origin-center rotate-[-45deg]'}`} style={{ transformOrigin: 'center', transform: percent < 0.5 ? `rotate(${percent * 270}deg)` : 'rotate(135deg)' }} /> // Simplified pan visual for now
                        ) : (
                            // Volume shows bottom up
                            <circle cx="16" cy="16" r="12" fill="none" stroke={color} strokeWidth="2" strokeDasharray="56.5" strokeDashoffset={56.5 - (percent * 56.5)} className="transition-all" />
                        )}
                    </svg>

                    {/* Indicator pointer */}
                    <div
                        className="absolute w-full h-full"
                        style={{ transform: `rotate(${angle}deg)` }}
                    >
                        <div className="mx-auto mt-1 w-1 h-2 rounded bg-white shadow-[0_0_2px_rgba(255,255,255,0.8)]" />
                    </div>
                </div>
                <span className="text-[8px] font-mono text-[#666] uppercase group-hover:text-[#AAA] transition-colors">{label}</span>
            </div>
        );
    };

    return (
        <div className="w-[200px] h-[60px] bg-[#1C1C1E] border-r border-y border-[#111] flex items-center justify-between px-2 shadow-inner shrink-0 z-30">
            {/* Left: Status / Name */}
            <div className="flex flex-col gap-1 w-20">
                <span className="text-[10px] font-bold truncate" style={{ color }}>{trackName}</span>
                <div className="flex gap-1">
                    {/* Mute Button */}
                    <button
                        onClick={() => setIsMuted(!isMuted)}
                        className={`px-1.5 py-0.5 rounded-sm text-[8px] font-bold tracking-widest transition-all ${isMuted ? 'bg-red-500/20 text-red-500 border border-red-500/50 glow-red' : 'bg-[#222] text-[#666] border border-[#333]'}`}
                    >
                        M
                    </button>
                    {/* Solo Button */}
                    <button
                        onClick={() => setIsSolo(!isSolo)}
                        className={`px-1.5 py-0.5 rounded-sm text-[8px] font-bold tracking-widest transition-all ${isSolo ? 'bg-yellow-500/20 text-yellow-500 border border-yellow-500/50 glow-yellow' : 'bg-[#222] text-[#666] border border-[#333]'}`}
                    >
                        S
                    </button>
                </div>
            </div>

            {/* Right: Knobs */}
            <div className="flex gap-2 items-center">
                {renderKnob(pan, -100, 100, 'PAN', setPan, true)}
                {renderKnob(volume, 0, 100, 'VOL', setVolume, false)}
            </div>
        </div>
    );
}
